@import "../../assets/styles/corporateDesign.css";

@tailwind base;
@tailwind components;
@tailwind utilities;

.temperatur {
    color: var(--dark) !important;
    font-weight: bolder;
    font-size: 70px !important;
    margin-bottom: 10px;
}

.aare-box-temperatur {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    transition: 0.3s;
}

.aare-box-temperatur:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.aare-box-wasser {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--dark);
    border-color: var(--primary);
    border-width: 6px;
}

.wasser-top {
    display: flex;
    flex-direction: row;
}

.aare-stadt {
    font-size: 24px;
    color: var(--dark);
    font-weight: bold !important;
}

.aare-info-temperatur {
    font-size: 18px;
    margin-bottom: 5px;
    color: var(--dark);
}

.aare-info-wasser {
    font-size: 18px;
    margin-bottom: 5px;
    color: var(--tetriary);
}

.forecasttemp {
    color: var(--dark) !important;
    font-weight: bolder;
    font-size: 30px !important;
    margin-bottom: 10px;
}

.kanalbild {
    max-width: 6.8rem;
    border-radius: 100%;
}

.aare-link {
    color: var(--dark);
}

@media (max-width: 768px) {
    .temperatur,
    .forecasttemp,
    .aare-info-temperatur {
        font-size: 14px !important;
    }
}
